 * @Description: - 定位模块详细配置页面
 * @Author: - xin.peng
 * @Date: - 2020年3月19日16:22:43
 **/

import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import dictList from "@/utils/dictList";
import api from "@/microApps/geographic/api";
import utils from "@/utils/utils";
import { ElForm } from "element-ui/types/form";

@Component({})
export default class DetailInfo extends Vue {
  @Prop({ type: String })
  id!: string;
  @Prop({ type: Object })
  baseInfo!: any;
  @Prop({ type: String, default: "add" })
  method!: string;
  @Prop({ type: Array, default: () => [] })
  orgTree!: any;

  baseForm: any = {
    enable: false
  };
  commonProps: any = {
    multiple: true,
    value: "id",
    label: "name",
    checkStrictly: true,
    emitPath: false
  };
  commonRules: any = {
    code: { required: true, trigger: "blur", message: "该字段不能为空" },
    name: { required: true, trigger: "blur", message: "该字段不能为空" },
    targetCenterCoordinate: { required: true, trigger: "change", message: "该字段不能为空" },
    targetCoordinateType: { required: true, trigger: "blur", message: "该字段不能为空" },
    scole: { required: true, trigger: "blur", message: "该字段不能为空" },
    targetScale: { required: true, trigger: "blur", message: "该字段不能为空" },
    typeDetailIds: { required: true, trigger: "change", message: "该字段不能为空" },
    url: { required: true, trigger: "blur", message: "该字段不能为空" }
  };
  typeDetails: any[] = [];
  locationConfigList: any[] = [];
  targetCoordinateTypeList: any[] = [];
  baseInfoHasSaved: boolean = false; // 默认为false，基本信息保存状态，method === preview状态不生效
  selectedConfigForm: any = {};

  get baseIsDisabled() {
    if (this.method === "preview") {
      return true;
    } else {
      return this.baseInfoHasSaved;
    }
  }
  // 添加一个新配置
  addConfigOption() {
    this.locationConfigList.forEach((i: any) => {
      this.$set(i, "isEditing", false);
    });
    this.locationConfigList.push({
      firstEdit: true,
      isEditing: true,
      locationId: this.baseForm.id,
      originalCoordinateType: "GCJ02"
    });
  }
  handleUpdate(key: number) {
    this.selectedConfigForm = { ...this.locationConfigList[key] };
    this.$set(this.locationConfigList[key], "isEditing", true);
  }
  handleCancel(key: number) {
    if (this.locationConfigList[key] && this.locationConfigList[key].id) {
      this.locationConfigList[key] = Object.assign({ isEditing: false }, this.selectedConfigForm);
      // this.$set(this.locationConfigList[key], "isEditing", false);
    } else {
      this.locationConfigList.splice(key, 1);
    }
  }
  backToList() {
    this.$emit("back");
    if (this.method === "add") {
      this.baseInfoHasSaved = false;
      this.$set(this, "baseForm", {});
      this.locationConfigList.length = 0;
    }
  }
  // 获取基础目标坐标系类型
  async getTargetType() {
    let {
      data: { data, status, message }
    } = await this.$http.post(dictList.requestUrl, {
      dictTypeCode: dictList.dict.targetCoordinateType
    });
    if (status) {
      utils.removeEmptyChildren(data);
      this.targetCoordinateTypeList = data;
    } else {
      this.$message.error("目标坐标系类型查询失败：" + message);
    }
  }
  // 获取基础数据类型
  async getBaseTypeTree() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.getBaseTypeTree);
    if (status) {
      this.typeDetails = data.map((o: any) => {
        return { ...o, disabled: true };
      });
    } else {
      this.$message.error(message);
    }
  }
  // 获取配置信息
  async getBaseInfo() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(api.location.getBaseInfo + this.id);
      if (status) {
        this.locationConfigList = data.locationConfigInfoList.map((o: any) => {
          return {
            ...o,
            typeDetailIds: o.infoTypeList.map((i: any) => i.typeDetailId)
          };
        });
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      console.log(e);
    }
  }
  // 保存基础信息
  async saveBaseInfo() {
    try {
      (this.$refs.baseFormRef as ElForm).validate(async (valid: boolean) => {
        if (valid) {
          let url: string = this.method === "add" ? api.location.saveBaseInfo : api.location.updateBaseInfo;
          let {
            data: { data, status, message }
          } = await this.$http.post(url, this.baseForm);
          if (status) {
            this.$message.success("保存成功");
            this.baseInfoHasSaved = true;
            if (this.method === "add") this.baseForm.id = data;
          } else {
            this.$message.error(message);
          }
        }
      });
    } catch (e) {
      console.log(e);
    }
  }
  // 保存配置项
  async saveLocationConfig(config: any, key: number) {
    try {
      let refs: any = this.$refs["configForm"];
      (refs[key] as ElForm).validate(async (valid: any) => {
        if (valid) {
          let url: string = config.firstEdit ? api.locationConfig.add : api.locationConfig.update;
          let {
            data: { data, status, message }
          } = await this.$http.post(url, this.locationConfigList[key]);
          if (status) {
            this.$message.success("保存成功");
            this.$set(this.locationConfigList[key], "isEditing", false);
            this.$set(this.locationConfigList[key], "firstEdit", false);
            this.$set(this.locationConfigList[key], "id", data);
          } else {
            this.$message.error(message);
          }
        }
      });
    } catch (e) {
      console.log(e);
    }
  }

  @Watch("id", { immediate: true })
  handleChange() {
    if (this.id) {
      this.baseForm = this.baseInfo;
      this.baseForm.orgIds = this.baseInfo.orgs.map((o: any) => o.orgId);
      this.getBaseInfo();
    } else {
      this.baseInfoHasSaved = false;
      this.$set(this, "baseForm", {});
      this.locationConfigList.length = 0;
    }
  }

  created() {
    this.getTargetType();
    this.getBaseTypeTree();
  }
}
