import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import echarts from "echarts";

@Component({})
export default class MarkerChart extends Vue {
  // 组件配置项
  @Prop({ type: Array, default: () => [] })
  markers!: any[]; // 坐标数组

  // 组件内部变量
  chart: any;

  created() {
    this.$nextTick(() => {
      this.chart = echarts.init(this.$refs.markerChart as any);
      // this.renderChart();
    });
  }

  beforeDestroy() {
    this.chart.dispose();
  }

  renderChart(data: any) {
    this.chart.setOption({
      xAxis: {},
      yAxis: {},
      series: [
        {
          symbolSize: 10,
          data: data.map((o: any) => o.position),
          type: "scatter"
        }
      ]
    });
  }

  @Watch("markers", { immediate: true, deep: true })
  handleChangeRender(val: any) {
    if (val && val.length) {
      setTimeout(() => this.renderChart(val), 100);
    } else {
      if (this.chart) {
        this.chart.clear();
      }
    }
  }
}
