import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import { loadAMap, loadAMapUI } from "common-config/esm/utils/loadAMap";

@Component({})
export default class QtouchAMapMarker extends Vue {
  // 组件配置项
  @Prop({ type: Array, default: () => [] })
  markers!: any[]; // 坐标数组

  // 组件内部变量
  map: any;
  selfMarkers: any[] = [];

  // 初始化时加载地图
  async created() {
    try {
      if (!(window as any).AMap) {
        await Promise.all([loadAMap(), loadAMapUI()]);
      }
      this.$nextTick(() => this.initMap());
    } catch (e) {
      console.log(e);
      this.$message.error("地图加载失败");
    }
  }

  beforeDestroy() {
    if (this.map) this.map.destroy();
  }

  initMap() {
    const mapConfig: any = JSON.parse(localStorage.getItem("tenantInfo") as string);
    this.map = new (window as any).AMap.Map(this.$refs["QAMapMarker"], {
      center: mapConfig.mapCenter.split(",") || [106, 28],
      zoom: mapConfig.mapZoom || 14,
      mapStyle: `amap://styles/${mapConfig.mapStyle || "default"}`
    });
  }

  initMarkers() {
    this.map && this.map.clearMap();
    this.selfMarkers.length = 0;
    this.markers.map((i: any) => {
      let marker = new (window as any).AMap.Marker({
        position: i.position
      });
      this.map.add(marker);
      this.selfMarkers.push(marker);
    });
    this.map.setFitView(this.selfMarkers);
  }

  @Watch("markers", { immediate: true, deep: true })
  handleChangeMarkers() {
    if (this.markers && this.markers.length) {
      setTimeout(() => this.initMarkers(), 100);
    } else {
      if (this.map) {
        this.map.clearMap();
      }
    }
  }
}
