 * @Description: -
 * @Author: -
 * @Date: -
 **/

import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import api from "@/microApps/geographic/api";
import QtouchAMapMarker from "@/components/qTouch/aMap/QtouchAMapMarker.vue";
import MarkerChart from "@/microApps/geographic/components/locationManage/MarkerChart.vue";

@Component({
  components: { MarkerChart, QtouchAMapMarker }
})
export default class ConfigPreview extends Vue {
  @Prop({ type: Object, required: true })
  baseInfo!: any;
  @Prop({ type: String, required: true })
  id!: string;

  baseForm: any = this.baseInfo || {}; // 基础信息
  locationConfigList: any[] = []; // 配置项list
  selectedTab: string = ""; // 当前选中tab也
  selectedDetailType: string = ""; // 选中数据类型
  selectedDataStatus: boolean = false; // 数据转换状态
  isLoading: boolean = true;
  graphicalType: string = "QtouchAMapMarker";

  selectedData: any = {
    oldType: "",
    oldPostion: [],
    transferType: "",
    transferPosition: []
  }; // 选中类型获取的数据转换成新旧坐标数组
  selectedMarkers: any[] = []; // 选中的显示数组

  // 获取配置信息
  async getBaseInfo() {
    try {
      this.isLoading = true;
      let {
        data: { data, status, message }
      } = await this.$http.get(api.location.getBaseInfo + this.id);
      if (status) {
        this.baseForm = data;
        this.locationConfigList = data.locationConfigInfoList.map((o: any) => {
          return {
            ...o,
            typeDetailIds: o.infoTypeList.map((i: any) => i.typeDetailId)
          };
        });
      } else {
        this.$message.error(message);
      }
    } finally {
      this.isLoading = false;
    }
  }

  /**
   * 根据基础数据类型id和配置项id查询对应数据
   * @param type 基础数据类型id
   * @param id 配置项id
   */
  async changeDataType(type: string, id: string) {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(api.locationConfig.preview, {
        configInfoId: id,
        typeDetailId: type
      });
      if (status) {
        if (data && data.length) {
          this.selectedData.oldPostion = data.map((o: any) => {
            return {
              position: [Number(o.originalX), Number(o.originalY)]
            };
          });
          this.selectedData.oldType = data[0].originalCoordinateType;
          this.selectedData.transferPosition = data.map((o: any) => {
            return {
              position: [Number(o.transformedX), Number(o.transformedY)]
            };
          });
          this.selectedData.transferType = data[0].targetCoordinateType;
        } else {
          this.$set(this.selectedData, "oldPostion", []);
          this.$set(this.selectedData, "transferPosition", []);
          this.$message.error("当前类型数据为空");
        }
        this.changeDataStatus(this.selectedDataStatus);
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      console.log(e);
    }
  }
  // tab栏点击事件，清空所有设置
  tabClickEv() {
    this.selectedDetailType = "";
    this.selectedDataStatus = false;
    this.graphicalType = "QtouchAMapMarker";
  }
  // 根据转换前后状态更新数据
  changeDataStatus(val: boolean) {
    this.selectedMarkers = val ? this.selectedData.transferPosition : this.selectedData.oldPostion;
    let type: string = val ? this.selectedData.transferType : this.selectedData.oldType;
    this.changeGraphicalType(type);
  }

  changeGraphicalType(type: string) {
    if (type === "2D") {
      this.graphicalType = "MarkerChart";
    } else {
      this.graphicalType = "QtouchAMapMarker";
    }
  }

  @Watch("id", { immediate: true })
  handleChange(val: any) {
    if (val) {
      this.getBaseInfo();
    }
  }
}
