import { Vue, Component, Watch } from "vue-property-decorator";
import Url from "@/microApps/geographic/api";
import utils from "@/utils/utils";
import DetailInfo from "@/microApps/geographic/components/locationManage/DetailInfo.vue";
import ConfigPreview from "@/microApps/geographic/components/locationManage/ConfigPreview.vue";

@Component({
  components: { ConfigPreview, DetailInfo },
  filters: {
    orgFilter: function(b: any) {
      if (b && b.length) {
        return b.map((o: any) => o.orgName).join("、");
      } else {
        return "";
      }
    }
  }
})
export default class LocationManagement extends Vue {
  searchVO: any = {
    current: 1,
    query: {
      name: "",
      orgIds: []
    },
    size: 10
  };
  total: number = 0;
  orgOptionsProps: any = {
    multiple: true,
    value: "id",
    label: "name",
    checkStrictly: true,
    emitPath: false
  };
  orgOptions: any[] = [];
  locationManagementList: any[] = [];
  detailComponentStatus: boolean = false;
  dataLoading: boolean = true;
  methodSelf: string = "add";
  selectedItem: any = {};
  detailComponentType: string = "DetailInfo";

  created() {
    // this.getLocationManagementList();
    this.getOrgList();
  }

  async getOrgList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(Url.organization.getOrgTreeData);
    if (status) {
      utils.removeEmptyChildren(data);
      this.orgOptions = data;
    } else {
      this.$message.error("组织机构列表查询失败：" + message);
    }
  }

  async getLocationManagementList() {
    try {
      this.dataLoading = true;
      let {
        data: { data, status, message }
      } = await this.$http.post(Url.location.getLocationManageList, this.searchVO);
      if (status) {
        this.locationManagementList = data.records;
        this.total = data.total;
      } else {
        this.$message.error(message);
      }
      this.dataLoading = false;
    } catch (e) {
      console.log(e);
    }
  }

  handleCurrentChange(num: number) {
    this.searchVO.current = num;
    this.getLocationManagementList();
  }

  editMethod(method: string, item?: any) {
    this.methodSelf = method;
    this.selectedItem = item;
    this.detailComponentStatus = true;
  }

  async handleDelete(item: any) {
    try {
      this.$confirm(`确认删除该 "${item.name}" 配置？`, "警告", {
        type: "warning",
        distinguishCancelAndClose: true,
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      }).then(async () => {
        let {
          data: { data, status, message }
        } = await this.$http.get(Url.location.deleteBaseInfo + item.id);
        if (status) {
          this.$message.success("删除成功");
          this.getLocationManagementList();
        } else {
          this.$message.error(message);
        }
      });
    } catch (e) {
      console.log(e);
    }
  }

  changeDetailComponent() {
    switch (this.methodSelf) {
      case "preview":
        this.detailComponentType = "ConfigPreview";
        break;
      case "add":
        this.detailComponentType = "DetailInfo";
        break;
      default:
        this.detailComponentType = "DetailInfo";
    }
  }

  @Watch("detailComponentStatus", { immediate: true })
  handleRequestServer(val: boolean) {
    if (!val) {
      this.methodSelf = "";
      this.getLocationManagementList();
    } else {
      this.changeDetailComponent();
    }
  }
}
